body {font-family: $font-family-serif;
  font-weight: 300;
  line-height: 2em;

}
 h1, h2, h3, h4, .h1, .h2, .h3 , .h4{
  font-family: $font-family-sans-serif;
  font-weight: bold;
  line-height: 1.5em;
}

h1,h2,h3,.h1,.h2,.h3{
    margin-top: 20px;
    margin-bottom: 10px;
}

h4,h5,h6,.h4,.h5,.h6{
    margin-top: 10px;
    margin-bottom: 10px;
	font-family: $font-family-sans-serif;
}
form {
	font-family: $font-family-sans-serif;
}
@media screen and (max-width:414px) {


	.offer-header h1.title, .offer-header .h1 {
		font-size: 1.5rem; 
	}
	
	h1.title small, .only-text small {
	
		font-size: 50%; 
	}
	
	h2 small ,h2 {
		line-height:1em !important;
		font-size:0.6em
	}
	
	.section-text p {
		padding-left:1.236em;
		padding-right:1.236em
	}
	
	h1.title, h1, .h1 {
	    font-size: 1.5em;
	    line-height: 1.15em;
	}

}

html *{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1, .h1 {
    font-size: $font-size-h1;
    line-height: 1.15em;
}
h2, .h2{
    font-size: $font-size-h2;
    line-height: 1.5em;
}
h3, .h3{
    font-size: $font-size-h3;
    line-height: 1.4em;
}
h4, .h4{
    font-size:1.5rem;
    line-height: 1.5em;
}
h5, .h5 {
    font-size: $font-size-h5;
    line-height: 1.55em;
    margin-bottom: 15px;
}
h6, .h6{
    font-size: $font-size-h6;
    text-transform: uppercase;
    font-weight: $font-weight-bold;
}
p{
font-family: $font-family-serif;
    font-size: $font-paragraph;
    margin: 0 0 10px;
	line-height:$font-paragraph*2.3;
}

b{
    font-weight: 700;
}

small,
.small{

font-family: linotype-sabon, sans-serif;
font-weight: 400;
font-style: italic;
    font-size: 75%;
    color: #777;
}

.title,
.card-title,
.info-title,
.footer-brand,
.footer-big h5,
.footer-big h4,
.media .media-heading{
    font-weight: $font-weight-extra-bold;
    font-family: $font-family-sans-serif;

    &,
    a{
        color: $black-color;
        text-decoration: none;
    }
}

h2.title{
    margin-bottom: $headings-margin-bottom * 2;
}
h5.description , h4.description{
font-family: linotype-sabon, sans-serif;

font-style: italic;

font-size:1.5em}
.description,
.card-description,
.footer-big p{
    color: $gray-light;
	
}

.text-warning {
  color: $brand-warning !important;
}
.text-primary {
  color: $brand-primary !important;
}
.text-danger {
  color: $brand-danger !important;
}
.text-success {
  color: $brand-success !important;
}
.text-info {
  color: $brand-info !important;
}
.text-rose{
    color: $brand-rose !important;
}
.text-gray{
    color: $gray-color !important;
}
.text-default {
	color:$black-color !important;
	}

.space{
    height: 130px;
    display: block;
}
.space-110{
    height: 110px;
    display: block;
}
.space-50{
    height: 50px;
    display: block;
}
.space-70{
    height: 70px;
    display: block;
}

.blockquote, blockquote{
    padding: 10px 20px;
    margin: 0 0 20px;
    border-left: 5px solid #eee;

    p{
        font-size: 1.063rem;
        font-style: italic;
    }

    small{
        display: block;
        font-size: 80%;
        line-height: 1.42857143;
        color: #777;
    }
}
